@use 'base/variables'as v;
@use 'base/mixins' as m;

.acciones{
    margin-top:  v.$separacion;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    a{
        color: v.$blanco;
        display: block;
       margin-top: calc(v.$separacion /2);
       text-align: center;
    } 
}